@import url(https://fonts.googleapis.com/css2?family=Nunito+Sans:wght@300;600;800&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root{--element: hsl(0, 0%, 100%);--input: hsl(0, 0%, 52%);--text: hsl(200, 15%, 8%);--background: hsl(0, 0%, 98%);--fs-homepage-items: 14px;--fs-detail-page: 16px;--ff: 'Nunito Sans', sans-serif;--fw-300: 300;--fw-600: 600;--fw-800: 800;--box-shadow:  0px 0px 20px 0px rgba(0,0,0,0.49)}body{min-width:375px;width:100%;height:100% !important;font-family:'Nunito Sans', sans-serif;font-family:var(--ff);background:hsl(0, 0%, 98%);background:var(--background)}header{background:hsl(0, 0%, 100%);background:var(--element);height:80px}.navbar{box-shadow:0px 1px 3px rgba(1,1,1,0.3);height:80px;padding:0 80px;display:flex;align-items:center}.nav-logo-title{color:hsl(200, 15%, 8%);color:var(--text);text-align:center;vertical-align:middle}.nav-logo-title p{font-weight:800 !important;font-weight:var(--fw-800) !important;margin-bottom:0;font-size:1.5rem}.theme-switch{background:transparent;border:none;color:hsl(200, 15%, 8%);color:var(--text);font-weight:800;font-weight:var(--fw-800)}.theme-switch span{margin-left:8px}.search-bar{padding:48px 80px;display:grid;grid-template-columns:auto 1fr auto}.search-bar .search-input{position:relative;height:60px;width:480px;background:hsl(0, 0%, 100%);background:var(--element);box-shadow:0px 0px 20px 0px rgba(0,0,0,0.49);box-shadow:var(--box-shadow);display:grid}.search-bar .search-input i{position:absolute;top:calc(50% - 9px);left:33px;transform:scale(1.3);color:hsl(200, 15%, 8%);color:var(--text)}.search-bar .search-input form{display:grid;align-items:center;padding:0 75px}.search-bar .search-input form input[type="text"]{border:none;outline:none;height:40px;background:hsl(0, 0%, 100%);background:var(--element);color:hsl(200, 15%, 8%);color:var(--text)}.search-bar .filter-region{justify-self:flex-end;position:relative}.search-bar .filter-region .filter-box{height:60px;width:200px;background:hsl(0, 0%, 100%);background:var(--element);box-shadow:0px 0px 20px 0px rgba(0,0,0,0.49);box-shadow:var(--box-shadow);display:flex;align-items:center;justify-content:center}.search-bar .filter-region .filter-box p{margin-bottom:0;margin-right:24px;color:hsl(200, 15%, 8%);color:var(--text)}.search-bar .filter-region .filter-box i{color:hsl(200, 15%, 8%);color:var(--text)}.search-bar .filter-region .filter-dropbox{position:absolute;top:65px;left:0px;border-radius:20px;display:none}.search-bar .filter-region .filter-dropbox.show{display:block}.search-bar .filter-region .filter-dropbox ul{list-style:none;background:hsl(0, 0%, 100%);background:var(--element);width:200px;padding:20px 25px}.search-bar .filter-region .filter-dropbox ul li{line-height:1.7em;font-weight:600;font-weight:var(--fw-600);width:100%}.search-bar .filter-region .filter-dropbox ul li button{display:block;text-decoration:none;color:hsl(200, 15%, 8%);color:var(--text);width:100%;position:relative;border:none;background:transparent;text-align:start}.search-bar .filter-region .filter-dropbox ul li button:hover::after{content:'';height:2px;width:100%;position:absolute;left:0;bottom:0;background:#aaa}.countries-section{padding:0 80px 45px;display:flex;flex-wrap:wrap;justify-content:space-evenly;grid-gap:75px}.country-card{height:340px;width:230px;background:hsl(0, 0%, 100%);background:var(--element);box-shadow:0px 0px 20px 0px rgba(0,0,0,0.49)}.country-card .country-card-header{height:160px;width:100%}.country-card .country-card-header img{width:100%;height:100%;object-fit:cover}.country-card .country-card-body{height:110px;padding:25px}.country-card .country-card-body p{margin:0;color:hsl(200, 15%, 8%);color:var(--text)}.country-card .country-card-body p:first-child{font-weight:800;font-weight:var(--fw-800);color:hsl(200, 15%, 8%);color:var(--text);margin-bottom:25px}.country-card .country-card-body p span{font-weight:600;font-weight:var(--fw-600)}.detail-header{height:200px;padding:76px 80px 0px}.detail-header button{background:hsl(0, 0%, 100%);background:var(--element);border:none;height:42px;width:140px;color:hsl(200, 15%, 8%);color:var(--text);box-shadow:0px 0px 20px 0px rgba(0,0,0,0.49);box-shadow:var(--box-shadow)}.detail-header button span{margin-left:20px;color:hsl(200, 15%, 8%);color:var(--text);font-weight:800;font-weight:var(--fw-800)}.detail-section{padding:0px 80px 76px;display:grid;grid-template-columns:1fr 1fr}.detail-section .country-flag{width:560px;height:400px;box-shadow:0px 0px 20px 0px rgba(0,0,0,0.49);box-shadow:var(--box-shadow)}.detail-section .country-flag img{width:560px;height:400px}.detail-section .country-details{display:grid;grid-template-columns:1fr 1fr;grid-template-rows:130px repeat(5, auto) 120px;grid-gap:5px;place-content:center}.detail-section .country-details p{color:hsl(200, 15%, 8%);color:var(--text);margin-bottom:0}.detail-section .country-details p span{font-weight:600;font-weight:var(--fw-600)}.detail-section .country-details p:first-child{grid-column:1/3;font-weight:800;font-weight:var(--fw-800);font-size:1.5rem;align-self:center}.detail-section .country-details p:nth-child(2){grid-column:1/2;grid-row:2/3}.detail-section .country-details p:nth-child(3){grid-column:2/3;grid-row:2/3}.detail-section .country-details p:nth-child(4){grid-column:1/2;grid-row:3/4}.detail-section .country-details p:nth-child(5){grid-column:2/3;grid-row:3/4}.detail-section .country-details p:nth-child(6){grid-column:1/2;grid-row:4/5}.detail-section .country-details p:nth-child(7){grid-column:2/3;grid-row:4/5}.detail-section .country-details p:nth-child(8){grid-column:1/2;grid-row:5/6}.detail-section .country-details p:nth-child(9){grid-column:1/2;grid-row:6/7}.detail-section .country-details p:nth-child(10){grid-column:1/3;grid-row:7/8;align-self:center;display:grid;grid-template-columns:auto 1fr}.detail-section .country-details p .border-country{display:inline-block;height:30px;background:hsl(0, 0%, 100%);background:var(--element);padding:5px 20px;margin-left:8px;margin-bottom:8px;box-shadow:0px 0px 20px 0px rgba(0,0,0,0.49);box-shadow:var(--box-shadow);display:inline-flex;align-items:center}.message-info p{color:hsl(200, 15%, 8%);color:var(--text);font-size:1.5rem;font-weight:800;font-weight:var(--fw-800);text-align:center}@media screen and (max-width: 768px){boyd{min-width:375px}header .navbar{padding:10px}.search-bar{grid-template-rows:1fr 1fr;grid-template-columns:none;grid-gap:20px;gap:20px;padding:10px}.search-bar .search-input{grid-row:1/2;width:100%}.search-bar .filter-region{grid-row:2/3;grid-column:1/2;justify-self:start}.detail-header{padding:10px}.detail-section{padding:10px;grid-template-rows:auto 1fr;grid-template-columns:none}.detail-section .country-flag{width:100%;height:auto}.detail-section .country-flag img{width:100%;height:auto}.detail-section .country-details{margin-top:20px;grid-row:2/3;display:flex;flex-direction:column}}

